import { Button, TextField, Typography, Card } from "@mui/material";
import React, { useState, useContext } from "react";
import { AppContext } from "../App";

import { createNewQuiz, joinQuiz } from "../methods";
const QuizStartPage = () => {
  const { setUser, defaultTimeout, setQuizId, quizId } = useContext(AppContext);
  const [name, setName] = useState("");

  const [toggleForm, setToggleForm] = useState(true);
  const startQuiz = async (e) => {
    e.preventDefault();
    if (name) {
      if (toggleForm) {
        //create a new quiz
        const id = await createNewQuiz({
          username: name,
          timeout: defaultTimeout,
        });

        setQuizId(id);
        setUser(name);
      } else {
        //join existing quiz
        const res = await joinQuiz({ username: name, quizId: quizId });

        if (res) setUser(name);
      }
    }
  };
  const toggleFormSwitch = () => {
    setToggleForm((prev) => !prev);
  };
  return (
    <div className="login_container">
      <Card
        style={{
          width: "fit-content",
          padding: 30,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <form className="form" onSubmit={startQuiz}>
          <Typography variant="h3" style={{ marginBottom: 30 }}>
            {toggleForm ? "Create a new Quiz" : "Join an existing quiz"}
          </Typography>
          <TextField
            label="Enter your name here"
            variant="outlined"
            name="username"
            onChange={(e) => setName(e.target.value)}
          />
          {!toggleForm && (
            <TextField
              label="Enter Quiz Id here"
              variant="outlined"
              name="username"
              onChange={(e) => setQuizId(e.target.value)}
            />
          )}

          <Button type="submit" variant="contained">
            Start Quiz
          </Button>

          <Button variant="outlined" onClick={toggleFormSwitch}>
            {toggleForm ? "Join an existing quiz" : "Create a new quiz"}
          </Button>
        </form>
      </Card>
    </div>
  );
};

export default QuizStartPage;
