import { Button, TextField, Typography, Card } from "@mui/material";
import React, { useState, useContext, useEffect } from "react";
import { AppContext } from "../App";
const QuizEndPage = () => {
  const { winnerName } = useContext(AppContext);
  const handleNewQuiz = (e) => {
    e.preventdefault();
    window.location.reload();
  };
  useEffect(() => {
    //fetch winnerName here
  }, []);
  return (
    <div className="login_container">
      <Card
        style={{
          width: "fit-content",
          padding: 30,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <form className="form" onSubmit={handleNewQuiz}>
          <Typography variant="h3" style={{ marginBottom: 30 }}>
            Timeout!
          </Typography>
          <Typography variant="p" style={{ marginBottom: 30 }}>
            Quiz has been ended. Here is the winner
          </Typography>
          <Typography variant="h5" style={{ marginBottom: 30 }}>
            {winnerName}
          </Typography>

          <Button type="submit" variant="contained">
            Start a new Quiz
          </Button>
        </form>
      </Card>
    </div>
  );
};

export default QuizEndPage;
