// const baseURL = "http://localhost:4000/";
const baseURL = "/";
const createNewQuiz = async (arg) => {
  const res = await fetch(baseURL + "createQuiz", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(arg),
    credentials: "include",
  });
  const data = await res.json();

  return data.id;
};
const joinQuiz = async (arg) => {
  const res = await fetch(baseURL + "joinQuiz", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(arg),
    credentials: "include",
  });
  const data = await res.json();

  return data.message == "Success" ? true : false;
};
const getRandomQuestion = async () => {
  const res = await fetch(baseURL + "getRandomQuestion");
  const data = await res.json();
  return data;
};

const verifySolution = async (arg) => {
  const res = await fetch(baseURL + "verifySolution", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(arg),
    credentials: "include",
  });
  const data = await res.json();
  return data;
};

export { createNewQuiz, joinQuiz, getRandomQuestion, verifySolution };
