import "./App.css";
import { useEffect, useState, createContext } from "react";
import QuizStartPage from "./components/QuizStartPage";
import QuizEndPage from "./components/QuizEndPage";
import QuestionsPage from "./components/QuestionsPage";

export const AppContext = createContext();
const defaultTimeout = 10; //10 mins

function App() {
  const [user, setUser] = useState(null);
  const [quizTimeout, setQuizTimeout] = useState(false);
  const [winnerName, setWinnerName] = useState("");
  const [quizId, setQuizId] = useState("");
  //we can use redux store also instead of maintaining states in each component

  useEffect(() => {
    console.log(user, quizId, quizTimeout);
  }, [user, quizId, quizTimeout]);
  return (
    <AppContext.Provider
      value={{
        user,
        setUser,
        defaultTimeout,
        setQuizTimeout,
        setQuizId,
        quizId,
        winnerName,
        setWinnerName,
      }}
    >
      {/* <QuestionsPage /> */}
      {user ? (
        quizTimeout ? (
          <QuizEndPage />
        ) : (
          <QuestionsPage />
        )
      ) : (
        <QuizStartPage />
      )}
    </AppContext.Provider>
  );
}

export default App;
