import {
  Button,
  TextField,
  Typography,
  Card,
  Snackbar,
  Alert,
} from "@mui/material";

import React, { useState, useContext, useEffect } from "react";
import { AppContext } from "../App";
import { getRandomQuestion, verifySolution } from "../methods";
import { io } from "socket.io-client";
// const socket = io("http://localhost:4000");
const socket = io();

const QuestionsPage = () => {
  const { user, quizId, setWinnerName, setQuizTimeout } =
    useContext(AppContext);
  const [correctAnsCount, setCorrectAnsCount] = useState(0);
  const [question, setQuestion] = useState({});
  const [answer, setAnswer] = useState("");

  useEffect(() => {
    socket.once("quiz-timeout", ({ winnerName, id }) => {
      if (id == quizId) {
        setWinnerName(winnerName);
        setQuizTimeout(true);
      }
    });
  }, []);

  const nextQuestion = async (e) => {
    e.preventDefault();

    if (question.id && answer) {
      const data = await verifySolution({
        id: question.id,
        answer,
        username: user,
        quizId,
      });
      if (data.isCorrect) setCorrectAnsCount((prev) => prev + 1);
      setAnswer("");
      getRandomQuestion().then((data) => {
        setQuestion(data);
      });
    }
  };

  useEffect(() => {
    getRandomQuestion().then((data) => {
      setQuestion(data);
    });
  }, []);

  return (
    <div className="login_container">
      <Typography style={{ position: "absolute", top: 10, left: 10 }}>
        Quiz Id: {quizId}
      </Typography>
      <Card
        style={{
          width: "fit-content",
          padding: 30,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <form className="form" onSubmit={nextQuestion}>
          <Typography
            variant="h6"
            style={{ marginBottom: 30, maxWidth: "60vw" }}
          >
            Q: {question.question}
          </Typography>
          <TextField
            label="Answer (Only Number)"
            variant="outlined"
            name="answer"
            type={"number"}
            value={answer}
            onChange={(e) => setAnswer(e.target.value)}
          />

          <Button type="submit" variant="contained" disabled={!answer}>
            Submit
          </Button>
        </form>
      </Card>
      <Snackbar open={true}>
        <Alert severity="info">#Correct Responses = {correctAnsCount}</Alert>
      </Snackbar>
    </div>
  );
};

export default QuestionsPage;
